<template>
  <div>
    <loader v-if="loading"></loader>
    <v-container v-if="!isResult && !loading" fluid>
      <v-layout row justify-center>
        <v-flex md4 class="text-sm-center">
          <div class="display-1 py-4">K-Fold Validation</div>
          <v-select :items="datasetOptions" v-model="dataset" label="Pilih Dataset" />
          <v-text-field label="K" v-model.number="k" type="number" min="2" max="10" />
          <v-btn @click="runKFold" dark large depressed color="cyan" class="my-4">Run!</v-btn>
        </v-flex>
      </v-layout>
    </v-container>
    <div v-if="!loading && isResult" class="pa-0 ma-0">
      <v-layout row>
        <v-flex md12>
          <div class="display-1 py-4 px-4">Hasil K-Fold Validation</div>
          <v-data-table :headers="testHeaders" :items="testResults" :total-items="testResults.length" hide-actions>
            <template v-slot:items="props">
              <tr>
                <td>{{props.index + 1}}</td>
                <td>{{props.item.tp}}</td>
                <td>{{props.item.tn}}</td>
                <td>{{props.item.fp}}</td>
                <td>{{props.item.fn}}</td>
                <td>{{props.item.accuracy}}</td>
                <td>{{props.item.precision}}</td>
                <td>{{props.item.recall}}</td>
                <td>{{props.item.miscRate}}</td>
              </tr>
            </template>
          </v-data-table>
        </v-flex>
      </v-layout>
    </div>
  </div>
</template>

<script>
import settings from '@/service/settings'
import Loader from '@/components/Loader.vue'
import testingAPI from '@/service/testingAPI'
import constants from '@/service/constants'
import { testing, accuracy, precision, recall, miscRate } from '@/service/testing'

function partition(data, k) {
  let parts = [...Array(k).keys()].map(i => {
    return []
  })
  data.forEach((x, idx) => {
    const partIdx = idx % k
    parts[partIdx].push(x)
  })
  return parts
}

export default {
  name: 'KFold',
  components: { Loader },
  data () {
    return {
      loading: false,
      isResult: false,
      datasetOptions: constants.optionsDataset,
      dataset: constants.defaultDataset,
      testingDataset: '',
      k: 3,
      testResults: [],
      testHeaders: [
        { text: 'Partisi', sortable: false },
        { text: 'TP', sortable: false },
        { text: 'TN', sortable: false },
        { text: 'FP', sortable: false },
        { text: 'FN', sortable: false },
        { text: 'Accuracy', sortable: false },
        { text: 'Precision', sortable: false },
        { text: 'Recall', sortable: false },
        { text: 'Misclafication Rate', sortable: false }
      ]
    }
  },
  computed: {
    testingDsetOptions () {
      return this.datasetOptions.filter(d => d !== this.trainingDataset)
    }
  },
  methods: {
    async runKFold () {
      this.loading = true
      const resultsDerived = await testingAPI.kfold(this.dataset, this.k)
      this.testResults = resultsDerived
      this.loading = false
      this.isResult = true
    }
  },
  mounted () {
    this.datasetOptions = settings.getOptionsDataset()
    this.dataset = settings.getActiveDataset()
  }
}
</script>

